.main-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 3px solid #39b54a;
  padding: 1rem 5rem;

  .left-side {
    display: flex;
    align-items: center;

    .menu-bars {
      cursor: pointer;
    }
  }

  ul {
    display: flex;

    li a {
      padding: 7px 10px;
      font-size: 1.2rem;
    }
  }

  .main-logo {
    height: 50px;
    width: 90px;
    padding: 5px;
    background: #39b54a;
  }
}
.active-link {
  color: rgba(0, 0, 0, 0.85);
}
.active-link:hover {
  color: inherit;
}

@media (max-width: 768px) {
  .main-nav {
    padding: 1rem 1rem;
  }
  .ui.inverted.icon.menu .item {
    text-align: left;
  }
}
